/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1.3;
  font-family: 'Cubic 11';
  background: #181E2A;
  color: #fff;
  position: relative;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  border: none;
  border-radius: 10px;
  font-size: 16px;
}

input:focus {
  outline: none;
}

a {
  color: #fff;
}

button {
  cursor: pointer;
  border: none;
  background: #0693E3;
  color: #fff;
  font-family: 'Cubic 11';
  padding: 24px;
  -webkit-box-shadow: 5px 5px rgba(255, 255, 255, 0.5);
          box-shadow: 5px 5px rgba(255, 255, 255, 0.5);
  position: relative;
  text-shadow: 1px 1px #000;
}

button::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 50%;
  left: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.1);
}

h1 {
  font-size: 60px;
}

h2 {
  font-size: 48px;
}

h3 {
  font-size: 36px;
}

h4 {
  font-size: 24px;
}

body,
h5,
p,
span,
button {
  font-size: 18px;
}

