@-webkit-keyframes twinkle {
  0%,
  100% {
    opacity: .5;
  }
  50% {
    opacity: 0;
  }
}

@keyframes twinkle {
  0%,
  100% {
    opacity: .5;
  }
  50% {
    opacity: 0;
  }
}

@-webkit-keyframes firework_scatter {
  0% {
    offset-distance: 0%;
  }
  50% {
    opacity: 1;
  }
  100% {
    offset-distance: 100%;
    opacity: 0;
  }
}

@keyframes firework_scatter {
  0% {
    offset-distance: 0%;
  }
  50% {
    opacity: 1;
  }
  100% {
    offset-distance: 100%;
    opacity: 0;
  }
}

@-webkit-keyframes firework_position {
  0%,
  19.9% {
    -webkit-transform: translate(10vw, 40vh);
            transform: translate(10vw, 40vh);
  }
  20%,
  39.9% {
    -webkit-transform: translate(40vw, 30vh);
            transform: translate(40vw, 30vh);
  }
  40%,
  59.9% {
    -webkit-transform: translate(20vw, 90vh);
            transform: translate(20vw, 90vh);
  }
  60%,
  79.9% {
    -webkit-transform: translate(30vw, 20vh);
            transform: translate(30vw, 20vh);
  }
  80%,
  99.9% {
    -webkit-transform: translate(80vw, 50vh);
            transform: translate(80vw, 50vh);
  }
}

@keyframes firework_position {
  0%,
  19.9% {
    -webkit-transform: translate(10vw, 40vh);
            transform: translate(10vw, 40vh);
  }
  20%,
  39.9% {
    -webkit-transform: translate(40vw, 30vh);
            transform: translate(40vw, 30vh);
  }
  40%,
  59.9% {
    -webkit-transform: translate(20vw, 90vh);
            transform: translate(20vw, 90vh);
  }
  60%,
  79.9% {
    -webkit-transform: translate(30vw, 20vh);
            transform: translate(30vw, 20vh);
  }
  80%,
  99.9% {
    -webkit-transform: translate(80vw, 50vh);
            transform: translate(80vw, 50vh);
  }
}

