.star {
  position: absolute;
  width: 0px;
  height: 0px;
  border-color: white transparent transparent transparent;
  border-width: 4.49px 6.18px;
  border-style: solid;
  -webkit-animation: twinkle 2s infinite;
          animation: twinkle 2s infinite;
}

.star::before {
  content: '';
  display: block;
  position: absolute;
  left: -6.18px;
  top: -4.49px;
  border-color: white transparent transparent transparent;
  border-width: 4.49px 6.18px;
  border-style: solid;
  -webkit-transform: rotate(72deg);
          transform: rotate(72deg);
  -webkit-transform-origin: 50% 22.5%;
          transform-origin: 50% 22.5%;
}

.star::after {
  content: '';
  display: block;
  position: absolute;
  left: -6.18px;
  top: -4.49px;
  border-color: white transparent transparent transparent;
  border-width: 4.49px 6.18px;
  border-style: solid;
  -webkit-transform: rotate(-72deg);
          transform: rotate(-72deg);
  -webkit-transform-origin: 50% 22.5%;
          transform-origin: 50% 22.5%;
}

